/************************************/
/* GLOBAL RESET */
/************************************/
*,
*::after,
*::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/*************************************/
/* GLOBAL STYLING */
/*************************************/
html {
  font-size: 62.5%;
  background-color: rgb(32, 33, 36);
  color: rgb(231, 228, 239);
  font-family: "Playfair Display", serif;

  scroll-behavior: smooth;
  overflow-x: hidden;
}

img {
  size: 100%;
}

.container {
  max-width: 80%;
  margin: 1rem auto;
  padding: 1rem 4rem;
  border-radius: 1.2rem;
  display: flex;
  justify-content: space-between;
}

/*************************************/
/* HEADER STYLING */
/*************************************/
.header {
  background-color: rgb(54, 48, 158);
  justify-content: flex-start;
  align-items: center;
  gap: 2rem;

  border-radius: 1.2rem;
}

.logo-img {
  width: 10rem;
  border-radius: 1.2rem;
}

.logo-text {
  font-size: 3rem;
  letter-spacing: 0.1rem;
  font-weight: 500;
  color: rgb(231, 228, 239);
  transition: all ease-in-out 0.3s;
}
.logo-text:hover {
  letter-spacing: 0.25rem;
}

/*************************************/
/* MAIN SECTION STYLING */
/*************************************/
.main {
  display: block;
  position: relative;
}
/*************************************/
/* rules STYLING */
/*************************************/
.rules-section {
  padding: 2rem;
  min-height: 100vh;
}
.welcome {
  margin: 1rem 0;
  padding: 1rem;
  font-size: 4rem;
  text-align: center;
  color: rgb(136, 216, 208);
}
.details {
  margin-bottom: 1rem;
  line-height: 120%;
  font-size: 2rem;
  text-align: center;
  color: rgb(137, 211, 132);
}
.made-by {
  font-size: 1.2rem;
  text-align: end;
  color: rgb(143, 255, 135);
  margin-right: 2rem;
}
.made-by em {
  letter-spacing: 0.1rem;
  font-weight: 900;
}
.list-header {
  font-size: 3rem;
  padding: 2rem 0;
  color: rgb(255, 215, 0, 0.8);
}
.rules-list {
  padding-left: 2rem;
  font-size: 2.5rem;
  text-align: justify;
}
.rule {
  padding-bottom: 1rem;
  color: rgb(255, 215, 0, 0.8);
}
.warning-words,
.lose-word {
  color: rgba(255, 0, 0, 0.8);
}
.win-word {
  color: rgb(0, 145, 0);
}
.play-btn {
  display: block;
  margin: 8rem auto 0;
  padding: 1rem 2rem;
  background: none;
  border: none;
  cursor: pointer;
}
.play-btn:focus {
  outline: none;
  box-shadow: 1px 1px 5px 5px rgba(54, 48, 158, 0.5);
}
.play-btn p {
  color: rgb(54, 48, 158);
  font-size: 2.5rem;
  font-weight: 800;
  margin-bottom: -1.5rem;
  letter-spacing: 0.2rem;
}

.material-symbols-outlined {
  display: block;
  font-size: 10rem;
  color: rgb(54, 48, 158);
  animation: arrow 1s ease-out alternate infinite;
}
@keyframes arrow {
  0% {
    transform: translateY(-10%);
  }
  100% {
    transform: translateY(0%);
  }
}

.top-btn {
  background: none;
  border: 2px solid rgb(54, 48, 158);
  border-radius: 1.2rem;
  color: rgb(54, 48, 158);
  cursor: pointer;
  padding: 0.2rem 0.1rem;
  font-size: 1.5rem;
  font-weight: 600;

  position: fixed;
  bottom: 10%;
  left: 85%;

  display: none;
}
.top-btn:focus {
  outline: none;
  box-shadow: 1px 1px 5px 5px rgba(54, 48, 158, 0.5);
}
.top-btn .material-symbols-outlined {
  font-size: 5rem;
  padding: 0.1rem 0;
  margin-bottom: -1rem;
}

/*************************************/
/* GAME STYLING */
/*************************************/
.turns {
  display: flex;
  justify-content: center;
  flex-direction: column;
  font-size: 3rem;
  width: 29%;

  gap: 5rem;
}
.turn,
.meteors-num {
  display: flex;
  padding: 2rem 6rem;
  border: 3px dashed rgb(54, 48, 158);
  letter-spacing: 1px;
  border-radius: 1.2rem;
  justify-content: center;
}
.section-game {
  display: flex;
  gap: 10rem;
  align-items: flex-start;
  margin-top: 4rem;
  justify-content: space-between;

  position: relative;
}

.tiles-container {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  margin: 12rem 0;

  min-width: 40%;
  border: 1px solid rgb(54, 48, 158);

  transform: rotate(45deg);
}

.tile {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
  border: 1px solid rgb(54, 48, 158);
  aspect-ratio: 1/1;
}

.tile-text {
  transform: rotate(-45deg);
  z-index: -1;
  pointer-events: none;
}

.destroyed {
  visibility: hidden;
}

.meteor {
  aspect-ratio: 1/1;
  border-radius: 50%;
  z-index: 2;
  cursor: grab;
}

.yellow {
  position: absolute;
  width: 37.8%;
  top: 30%;
  right: 70%;

  display: none;
  justify-content: center;
  align-items: center;
  border: 1px solid rgb(235, 231, 38);
  touch-action: none;
  -ms-touch-action: none;
}
.yellow-inner {
  width: 50%;
  border-radius: 50%;
  aspect-ratio: 1/1;
  pointer-events: none;

  background-image: url("./../imgs/yellow-meteor.jfif");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  touch-action: none;
  -ms-touch-action: none;
}

.blue {
  position: absolute;
  width: 4.5%;
  left: 8%;
  bottom: 30%;
  background-image: url("./../imgs/blue-meteor.jfif");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  touch-action: none;
  -ms-touch-action: none;
}
/*************************************/
/* POPUP STYLING */
/*************************************/
.pop-up {
  top: 28%;
  margin: 0 auto;
  padding: 2rem 4rem;
  font-size: 5rem;
  font-weight: bold;
  border-radius: 1.2rem;
  border-color: rgb(231, 228, 239);
  background-color: rgb(231, 228, 239);
  color: rgb(54, 48, 158);
}
.pop-up[open] {
  animation: show 0.5s ease normal;
}

.pop-up::backdrop {
  background-color: rgba(54, 48, 158, 0.1);
}

@keyframes show {
  from {
    transform: translateY(-110%);
  }

  to {
    transform: translateY(0%);
  }
}

.restart-btn {
  display: block;
  margin: 3rem auto 1rem;
  padding: 1rem 2rem;
  font-size: 2rem;
  font-weight: 600;
  border-radius: 1.2rem;
  border-color: rgba(255, 0, 0, 0);

  color: rgb(231, 228, 239);
  background-color: rgba(255, 0, 0, 0.8);
  cursor: pointer;
  transition: all 0.1s ease;
  box-shadow: 0 5px 5px rgb(0, 0, 0, 0.7);
}
.restart-btn:focus {
  outline: none;
  box-shadow: 1px 1px 5px 5px rgba(54, 48, 158, 0.5);
}
.restart-btn:active {
  transform: translateY(1px);
  box-shadow: 0 3px 5px rgb(0, 0, 0, 0.7);
}
.reset-btn {
  padding: 0.5rem 2.5rem;
  font-size: 2rem;
  font-weight: 700;
  color: rgba(54, 48, 158);
  background: none;
  border-radius: 1.2rem;
  border: 2px solid rgba(54, 48, 158);
  box-shadow: 0 5px 10px rgb(0, 0, 0, 0.5);

  transition: all 0.1s ease;

  visibility: hidden;
  pointer-events: none;
  cursor: pointer;
}
.reset-btn:focus {
  outline: none;
  box-shadow: 1px 1px 5px 5px rgba(54, 48, 158, 0.5);
}
.reset-btn:active {
  transform: translateY(1px);
  box-shadow: 0 3px 5px rgb(0, 0, 0, 0.7);
}

/*************************************/
/* FOOTER STYLING */
/*************************************/
.footer {
  padding: 2rem 4rem;
  font-size: 1.4rem;
  font-weight: 500;
  background-color: rgb(54, 48, 158);
}

.contact-me_link,
.contact-me_link:link,
.contact-me_link:visited {
  display: inline-block;
  text-decoration: none;
  color: rgb(231, 228, 239);
  transition: transform 0.3s;
}

.contact-me_link:hover,
.contact-me_link:active {
  text-decoration: underline;
  transform: scale(1.1);
}

/*************************************/
/* MEDIA QUERIES */
/*************************************/

@media screen and (max-width: 1780px) {
  .rules-list {
    font-size: 2rem;
  }
  .section-game {
    padding-top: 5rem;
    margin-top: 0;
  }
  .turns {
    font-size: 2.5rem;
  }
  .yellow {
    top: 35%;
    right: 75%;
  }
}
@media screen and (max-width: 1200px) {
  .play-btn {
    margin-top: 0;
  }
  .container {
    max-width: 100%;
  }
  .turn,
  .meteors-num {
    padding: 2rem 2rem;
  }
  .blue {
    left: 12%;
    bottom: 40%;
  }
  .yellow {
    top: 48%;
    right: 65%;
  }
  .top-btn {
    bottom: 12%;
    left: 90%;
  }
}
@media screen and (max-width: 1000px) {
  .container {
    padding: 1rem 2rem;
  }
  .section-game {
    justify-content: space-evenly;
    gap: 0;
  }
  .tile {
    font-size: 2rem;
  }
  .turns {
    width: 22%;
    font-size: 1.5rem;
  }
  .turn,
  .meteors-num {
    padding: 1rem 2rem;
  }
  .yellow {
    top: 40%;
    right: 60%;
  }
  .blue {
    left: 20%;
  }
  .top-btn {
    bottom: 12%;
    left: 90%;
    font-size: 1rem;
  }
  .play-btn {
    margin: 0 auto;
  }
  .reset-btn {
    position: absolute;
  }
  .rules-list {
    font-size: 1.5rem;
  }
}

@media screen and (max-width: 760px) {
  .logo-text {
    font-size: 2.6rem;
  }
  .rules-list {
    font-size: 1.5rem;
  }
  .section-game {
    margin: 0 1rem;
    gap: 0;
  }
  .tile {
    font-size: 1.5rem;
  }
  .turns {
    font-size: 1.5rem;
    width: 30%;
  }
  .turn,
  .meteors-num {
    padding: 1rem 2rem;
  }

  .top-btn {
    bottom: 12%;
    left: 90%;
    font-size: 1rem;
  }
  .play-btn {
    margin: 0 auto;
  }
  .top-btn .material-symbols-outlined {
    font-size: 4rem;
  }
  .reset-btn {
    font-size: 1.5rem;
    padding: 0.5rem 1rem;
  }
}
@media screen and (max-width: 640px) {
  .logo-text {
    font-size: 2.6rem;
  }
  .welcome {
    font-size: 2.5rem;
  }
  .tile {
    font-size: 1.2rem;
  }
  .turns {
    font-size: 1.2rem;
    width: 35%;
  }
  .turn,
  .meteors-num {
    padding: 1rem 2rem;
    white-space: nowrap;
  }
  .reset-btn {
    left: 50%;
  }
  .pop-up {
    font-size: 4rem;
  }
}
@media screen and (max-width: 520px) {
  .section-game {
    display: block;
  }
  .tiles-container {
    max-width: min-content;
    min-width: 50%;
    margin: 8rem 3rem 4rem auto;
  }
  .tile {
    font-size: 2rem;
  }
  .turns {
    font-size: 1.5rem;
    gap: 1rem;
    width: 65%;
  }
  .turn,
  .meteors-num {
    padding: 1rem;
  }
  .reset-btn {
    top: 18%;
    left: 75%;
  }
  .yellow {
    top: 70%;
    left: -15%;
    width: 55%;
  }

  .blue {
    bottom: 0%;
    left: 10%;
    width: 9%;
  }
  .logo-text {
    font-size: 1.2rem;
  }
  .logo-img {
    width: 6rem;
  }
  .welcome {
    font-size: 1.5rem;
  }
  .top-btn {
    left: 85%;
  }
  .rules-list {
    font-size: 1.2rem;
  }
}
@media screen and (max-width: 320px) {
  .section-game {
    display: block;
  }
  .tile {
    font-size: 1.5rem;
  }
  .tiles-container {
    max-width: min-content;
    min-width: 40%;
    margin: 6rem auto 3rem;
  }

  .header {
    gap: 1rem;
  }
  .logo-text {
    font-size: 1rem;
  }
  .logo-img {
    width: 3rem;
  }
  .welcome {
    font-size: 1.5rem;
  }
  .top-btn {
    left: 80%;
  }
  .top-btn .material-symbols-outlined {
    font-size: 2.5rem;
  }
  .turns {
    font-size: 1.5rem;
    gap: 1rem;
    width: 70%;
  }
  .turn,
  .meteors-num {
    padding: 0.5rem;
  }
  .reset-btn {
    top: 25%;
    left: 75%;
  }
  .rules-list {
    font-size: 1.2rem;
  }
  .rules-section {
    padding: 0;
  }
  .yellow {
    top: 38%;
    left: -6%;
    width: 38%;
  }
  .blue {
    bottom: 0%;
    left: 10%;
    width: 9%;
  }
  .footer {
    font-size: 1rem;
  }
  .pop-up {
    font-size: 2.5rem;
    text-align: center;
  }
}
