*, :after, :before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  color: #e7e4ef;
  scroll-behavior: smooth;
  background-color: #202124;
  font-family: Playfair Display, serif;
  font-size: 62.5%;
  overflow-x: hidden;
}

img {
  size: 100%;
}

.container {
  max-width: 80%;
  border-radius: 1.2rem;
  justify-content: space-between;
  margin: 1rem auto;
  padding: 1rem 4rem;
  display: flex;
}

.header {
  background-color: #36309e;
  border-radius: 1.2rem;
  justify-content: flex-start;
  align-items: center;
  gap: 2rem;
}

.logo-img {
  width: 10rem;
  border-radius: 1.2rem;
}

.logo-text {
  letter-spacing: .1rem;
  color: #e7e4ef;
  font-size: 3rem;
  font-weight: 500;
  transition: all .3s ease-in-out;
}

.logo-text:hover {
  letter-spacing: .25rem;
}

.main {
  display: block;
  position: relative;
}

.rules-section {
  min-height: 100vh;
  padding: 2rem;
}

.welcome {
  text-align: center;
  color: #88d8d0;
  margin: 1rem 0;
  padding: 1rem;
  font-size: 4rem;
}

.details {
  text-align: center;
  color: #89d384;
  margin-bottom: 1rem;
  font-size: 2rem;
  line-height: 120%;
}

.made-by {
  text-align: end;
  color: #8fff87;
  margin-right: 2rem;
  font-size: 1.2rem;
}

.made-by em {
  letter-spacing: .1rem;
  font-weight: 900;
}

.list-header {
  color: #ffd700cc;
  padding: 2rem 0;
  font-size: 3rem;
}

.rules-list {
  text-align: justify;
  padding-left: 2rem;
  font-size: 2.5rem;
}

.rule {
  color: #ffd700cc;
  padding-bottom: 1rem;
}

.warning-words, .lose-word {
  color: #f00c;
}

.win-word {
  color: #009100;
}

.play-btn {
  cursor: pointer;
  background: none;
  border: none;
  margin: 8rem auto 0;
  padding: 1rem 2rem;
  display: block;
}

.play-btn:focus {
  outline: none;
  box-shadow: 1px 1px 5px 5px #36309e80;
}

.play-btn p {
  color: #36309e;
  letter-spacing: .2rem;
  margin-bottom: -1.5rem;
  font-size: 2.5rem;
  font-weight: 800;
}

.material-symbols-outlined {
  color: #36309e;
  font-size: 10rem;
  animation: 1s ease-out infinite alternate arrow;
  display: block;
}

@keyframes arrow {
  0% {
    transform: translateY(-10%);
  }

  100% {
    transform: translateY(0%);
  }
}

.top-btn {
  color: #36309e;
  cursor: pointer;
  background: none;
  border: 2px solid #36309e;
  border-radius: 1.2rem;
  padding: .2rem .1rem;
  font-size: 1.5rem;
  font-weight: 600;
  display: none;
  position: fixed;
  bottom: 10%;
  left: 85%;
}

.top-btn:focus {
  outline: none;
  box-shadow: 1px 1px 5px 5px #36309e80;
}

.top-btn .material-symbols-outlined {
  margin-bottom: -1rem;
  padding: .1rem 0;
  font-size: 5rem;
}

.turns {
  width: 29%;
  flex-direction: column;
  justify-content: center;
  gap: 5rem;
  font-size: 3rem;
  display: flex;
}

.turn, .meteors-num {
  letter-spacing: 1px;
  border: 3px dashed #36309e;
  border-radius: 1.2rem;
  justify-content: center;
  padding: 2rem 6rem;
  display: flex;
}

.section-game {
  justify-content: space-between;
  align-items: flex-start;
  gap: 10rem;
  margin-top: 4rem;
  display: flex;
  position: relative;
}

.tiles-container {
  min-width: 40%;
  border: 1px solid #36309e;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  margin: 12rem 0;
  display: grid;
  transform: rotate(45deg);
}

.tile {
  aspect-ratio: 1 / 1;
  border: 1px solid #36309e;
  justify-content: center;
  align-items: center;
  font-size: 3rem;
  display: flex;
}

.tile-text {
  z-index: -1;
  pointer-events: none;
  transform: rotate(-45deg);
}

.destroyed {
  visibility: hidden;
}

.meteor {
  aspect-ratio: 1 / 1;
  z-index: 2;
  cursor: grab;
  border-radius: 50%;
}

.yellow {
  width: 37.8%;
  touch-action: none;
  -ms-touch-action: none;
  border: 1px solid #ebe726;
  justify-content: center;
  align-items: center;
  display: none;
  position: absolute;
  top: 30%;
  right: 70%;
}

.yellow-inner {
  width: 50%;
  aspect-ratio: 1 / 1;
  pointer-events: none;
  touch-action: none;
  -ms-touch-action: none;
  background-image: url("yellow-meteor.6d689f4d.jfif");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 50%;
}

.blue {
  width: 4.5%;
  touch-action: none;
  -ms-touch-action: none;
  background-image: url("blue-meteor.d239c6da.jfif");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  bottom: 30%;
  left: 8%;
}

.pop-up {
  color: #36309e;
  background-color: #e7e4ef;
  border-color: #e7e4ef;
  border-radius: 1.2rem;
  margin: 0 auto;
  padding: 2rem 4rem;
  font-size: 5rem;
  font-weight: bold;
  top: 28%;
}

.pop-up[open] {
  animation: .5s show;
}

.pop-up::backdrop {
  background-color: #36309e1a;
}

@keyframes show {
  from {
    transform: translateY(-110%);
  }

  to {
    transform: translateY(0%);
  }
}

.restart-btn {
  color: #e7e4ef;
  cursor: pointer;
  background-color: #f00c;
  border-color: #f000;
  border-radius: 1.2rem;
  margin: 3rem auto 1rem;
  padding: 1rem 2rem;
  font-size: 2rem;
  font-weight: 600;
  transition: all .1s;
  display: block;
  box-shadow: 0 5px 5px #000000b3;
}

.restart-btn:focus {
  outline: none;
  box-shadow: 1px 1px 5px 5px #36309e80;
}

.restart-btn:active {
  transform: translateY(1px);
  box-shadow: 0 3px 5px #000000b3;
}

.reset-btn {
  color: #36309e;
  visibility: hidden;
  pointer-events: none;
  cursor: pointer;
  background: none;
  border: 2px solid #36309e;
  border-radius: 1.2rem;
  padding: .5rem 2.5rem;
  font-size: 2rem;
  font-weight: 700;
  transition: all .1s;
  box-shadow: 0 5px 10px #00000080;
}

.reset-btn:focus {
  outline: none;
  box-shadow: 1px 1px 5px 5px #36309e80;
}

.reset-btn:active {
  transform: translateY(1px);
  box-shadow: 0 3px 5px #000000b3;
}

.footer {
  background-color: #36309e;
  padding: 2rem 4rem;
  font-size: 1.4rem;
  font-weight: 500;
}

.contact-me_link, .contact-me_link:link, .contact-me_link:visited {
  color: #e7e4ef;
  text-decoration: none;
  transition: transform .3s;
  display: inline-block;
}

.contact-me_link:hover, .contact-me_link:active {
  text-decoration: underline;
  transform: scale(1.1);
}

